import * as React from "react"; 
import {Navbar, Pagination, Container} from 'react-bootstrap'

export default function CreateBottomNav(props) {

    let pageItemData = [1,2,3,4,5,6,7]
    return(<Navbar 
            id="bottomNav"
            fixed="bottom"
            >
            <Container>
                <Pagination className="pagination-bottom">
                
                <Pagination.Prev 
                    className="prev-btn"
                    onClick={e => (props.activeTab===1) ? null : props.setActiveTab(props.activeTab-1)}
                    disabled={props.activeTab===1}
                />
                {pageItemData.map(currentTab => <Pagination.Item 
                    key={`btnTab_${currentTab}`}
                    onClick={e => props.setActiveTab(currentTab)}
                    active={currentTab===props.activeTab}>
                        {currentTab}
                    </Pagination.Item>)}
                <Pagination.Next
                    className="next-btn" 
                    onClick={e => (props.activeTab===7) ? null : props.setActiveTab(props.activeTab+1)} 
                    disabled={props.activeTab===7}
                />
                
                </Pagination>
            </Container>
        </Navbar>)
}