import React, {Component, useState} from "react";
import * as yup from 'yup';
import {Formik} from 'formik';
import { Row, Col, Button, Form} from 'react-bootstrap'
import CreatableSelect from 'react-select/creatable';
import { OnChangeValue } from 'react-select';

import FormModal from './FormModal'
import FormTable from './FormTable'
import FotoUploader from './FotoUploader'

export default function FormCreate(props) {

  const [show, setShow] = useState(false)
  const [modalMode, setModalMode] = useState(null)


  const [jobList] = useState([])
  const [studyList] = useState([])
  const [lingueList, setLingueList] = useState([])
  const [competenzeList, setCompetenzeList] = useState([])
  const [fotoData, setFotoData] = useState(null)


  function showModalHandlerFactory() {
    setShow(!show)
    setModalMode('factory')
  }
  function showModalHandlerStudy() {
    setShow(!show)
    setModalMode('study')
  }
  function addJob(props) {
    jobList.push(props)
    setShow(false)
  }
  function addStudy(props) {
    studyList.push(props)
    setShow(false)
  }
  function removeJob(number) {
    
    jobList.splice(number,1) 

  }
  function removeStudy(number) {
    studyList.splice(number,1)
  }

  function submitForm(formData) {
    props.setResumeData({
      jobList:jobList,
      studyList: studyList,
      competenzeList: competenzeList,
      lingueList: lingueList,
      fotoData: fotoData,
      formData: formData
    })

    window.location.hash = '#/resume'
  }


  return (
    <Formik
      validationSchema={yup.object().shape({
        firstName: yup.string()
         .min(2, 'Troppo corto!')
         .max(50, 'Troppo Lungo!')
         .required('Campo obbligatorio'),
        lastName: yup.string()
         .min(2, 'Troppo corto!')
         .max(50, 'Troppo Lungo!')
         .required('Campo obbligatorio'),
        email: yup.string().email('Email non valida').required('Campo obbligatorio'),
        telefono: yup
          .string()
          .matches(/[0-9 ]{10}/, {
            message: "Numero non valido",
          }),
        indirizzo: yup.string()
         .min(2, 'Troppo corto!')
         .max(50, 'Troppo Lungo!'),
        citta: yup.string()
         .min(2, 'Troppo corto!')
         .max(50, 'Troppo Lungo!')
         .required('Campo obbligatorio'),
        cap: yup
          .string()
          .matches(/[0-9 ]{5}/, {
            message: "Cap non valido",
          }),
        descrizione: yup.string().required(),
        terms: yup.bool().required().oneOf([true], 'Terms must be accepted'),
})}
      onSubmit={(data) => submitForm(data)}
      initialValues={{
        firstName: '',
        lastName: '',
        email:'',
        telefono: '',
        indirizzo: '',
        citta: '',
        cap: '',
        descrizione:'',
        terms: false,
      }}
    >
      {({
        handleSubmit,
        handleChange,
        handleBlur,
        values,
        touched,
        isValid,
        errors,
      }) => (
        <Form noValidate onSubmit={handleSubmit} >

          <FormModal 
            show={show} 
            onHide={showModalHandlerFactory} 
            modalMode={modalMode} 
            addJob={addJob}
            addStudy={addStudy}
          />
        
          <Row id="tab_1" className={(props.activeTab === 1) ? 'form-section' : 'd-none'} >
            <h4>Informazioni personali</h4>

            <Form.Group as={Col} md="6" controlId="validationFormik01">
              <Form.Label>Nome</Form.Label>
              <Form.Control
                type="text"
                name="firstName"
                value={values.firstName}
                onChange={handleChange}
                isValid={touched.firstName && !errors.firstName}
                isInvalid={!!errors.firstName}

              />
              <Form.Control.Feedback type="invalid">{errors.firstName}</Form.Control.Feedback>
            </Form.Group>
            <Form.Group as={Col} md="6" controlId="validationFormik02">
              <Form.Label>Cognome</Form.Label>
              <Form.Control
                type="text"
                name="lastName"
                value={values.lastName}
                onChange={handleChange}
                isValid={touched.lastName && !errors.lastName}
                isInvalid={!!errors.lastName}

              />

              <Form.Control.Feedback type="invalid">{errors.lastName}</Form.Control.Feedback>

            </Form.Group>
            <Form.Group as={Col} md="12" className="pt-3" controlId="validationFormikUsername" >
              <FotoUploader setFotoData={setFotoData}/>
            </Form.Group>
          </Row>

          <Row id="tab_2" className={(props.activeTab === 2) ? 'form-section' : 'd-none'}>
            <Form.Group as={Col} md="6" controlId="validationFormikEmail">
              <Form.Label>Email</Form.Label>
              <Form.Control
                type="email"
                name="email"
                value={values.email}
                onChange={handleChange}
                isValid={touched.email && !errors.email}
                isInvalid={!!errors.email}

              />
              <Form.Control.Feedback type="invalid">
                  {errors.email}
                </Form.Control.Feedback>
            </Form.Group>
            <Form.Group as={Col} md="6" controlId="validationFormikEmail">
              <Form.Label>Telefono</Form.Label>
              <Form.Control
                type="tel"
                name="telefono"
                value={values.telefono}
                onChange={handleChange}
                isInvalid={!!errors.telefono}

              />
              <Form.Control.Feedback type="invalid">
                  {errors.telefono}
                </Form.Control.Feedback>
            </Form.Group>
          </Row>
          <Row id="tab_3" className={(props.activeTab === 3) ? 'form-section' : 'd-none'}>
            <Form.Group as={Col} md="6" controlId="validationFormik03">
              <Form.Label>Indirizzo</Form.Label>
              <Form.Control
                type="text"
                placeholder="Es. Via dei magalli 46"
                name="indirizzo"
                value={values.indirizzo}
                onChange={handleChange}
                isInvalid={!!errors.indirizzo}
              />

              <Form.Control.Feedback type="invalid">
                {errors.indirizzo}
              </Form.Control.Feedback>
            </Form.Group>
            <Form.Group as={Col} md="3" controlId="validationFormik04">
              <Form.Label>Città</Form.Label>
              <Form.Control
                type="text"
                placeholder="Città"
                name="citta"
                value={values.citta}
                onChange={handleChange}
                isInvalid={!!errors.citta}
                isValid={touched.citta && !errors.citta}
              />
              <Form.Control.Feedback></Form.Control.Feedback>

              <Form.Control.Feedback type="invalid">
                {errors.citta}
              </Form.Control.Feedback>
            </Form.Group>
            <Form.Group as={Col} md="3" controlId="validationFormik05">
              <Form.Label>CAP</Form.Label>
              <Form.Control
                type="text"
                placeholder="cap"
                name="cap"
                value={values.cap}
                onChange={handleChange}
                isInvalid={!!errors.cap}
              />

              <Form.Control.Feedback type="invalid">
                {errors.cap}
              </Form.Control.Feedback>
            </Form.Group>
          </Row>

          <Row id="tab_4" className={(props.activeTab === 4) ? 'form-section' : 'd-none'}>
            <Form.Group as={Col} md="12" controlId="validationFormik06">
              <Form.Label>Descrizione</Form.Label>
              <Form.Control
                type="text"
                name="descrizione"
                value={values.descrizione}
                onChange={handleChange}
                isInvalid={!!errors.descrizione}
                isValid={touched.descrizione && !errors.descrizione}
              />
              <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
              <Form.Control.Feedback type="invalid">
                {errors.descrizione}
              </Form.Control.Feedback>
            </Form.Group>
          </Row>

          <Row id="tab_5" className={(props.activeTab === 5) ? 'form-section' : 'd-none'}>
           <h4>Informazioni Professionali</h4>

           <Col>
            <div className="d-flex justify-content-between align-items-center mb-3">
              <h5>Esperienze lavorative</h5>
              <Button onClick={showModalHandlerFactory} variant="outline-primary">Aggiungi</Button>
            </div>
            <FormTable 
            key={0}
              mode={'factory'}
              dataForTable={jobList}
              removeItem={removeJob}
            />
           </Col> 
           <Col>
           <div className="d-flex justify-content-between align-items-center mb-3">
            <h5>Titoli di studio </h5>
             <Button onClick={showModalHandlerStudy} variant="outline-primary">Aggiungi</Button>
           </div>
           <FormTable 
            key={1}

            mode={'study'}
            dataForTable={studyList}
            removeItem={removeStudy}

           />
           </Col> 
          </Row>

          <Row id="tab_6" className={(props.activeTab === 6) ? 'form-section' : 'd-none'}>
            <Col md={6}>
              <p>Seleziona Competenze:</p>
              <CreateSelect handleChange={setCompetenzeList}/>
            </Col> 
            <Col md={6}>
              <p>Seleziona lingue:</p>
              <CreateSelect handleChange={setLingueList}/>
            </Col> 
          </Row>
          <div id="tab_7" className={(props.activeTab === 7) ? 'form-section' : 'd-none'}>
            <Form.Group className="mb-3">
              <Form.Check
                required
                name="terms"
                label="Agree to terms and conditions"
                onChange={handleChange}
                isInvalid={!!errors.terms}
                feedback={errors.terms}
                feedbackType="invalid"
                id="validationFormik0"
              />
            </Form.Group>
            <Button type="submit">Submit form</Button>
          </div>
          
        </Form>
      )}
    </Formik>
  );
}




class CreateSelect extends Component<{}> {

  handleChange = (
    newValue: OnChangeValue<ColourOption, true>
  ) => {
    this.props.handleChange(newValue)
  };
  render() {
    return (
      <CreatableSelect
        isMulti
        onChange={this.handleChange}
      />
    );
  }
}