import React from 'react';
import { renderToString } from 'react-dom/server';
import {Button} from 'react-bootstrap'

import jsPDF from 'jspdf';

const SkillSection = ({resumeData}) => (
    <div >
      <h3>Competeze lavorative:</h3>
      <ul>
        {resumeData.competenzeList.map((element,index)=><li key={index}>{element.value}</li>)}
      </ul>
      <h3>Lingue:</h3>
      <ul>
        {resumeData.lingueList.map((element,index)=><li key={index}>{element.value}</li>)}
      </ul>
    </div>
);

const EsperienzeSection = ({resumeData}) => (
    <div>
      <h2>Esperienze Lavorative: </h2>
      {
        resumeData.jobList.map((element, index) => {
          let {locationName, roleName, startDate, endDate} = element
          startDate = startDate.toISOString().slice(0, 10)
          endDate = endDate.toISOString().slice(0, 10)
          return( 
                  <div key={index}>
                    <p>dal {startDate} al {endDate}</p>
                    <h3>{locationName}</h3>
                    <p>{roleName}</p>
                  </div>
                )
        })
      }
      
      <h2>Esperienze Scolastiche: </h2>
      {
        resumeData.studyList.map((element, index) => {
          let {locationName, roleName, startDate, endDate} = element
          startDate = startDate.toISOString().slice(0, 10)
          endDate = endDate.toISOString().slice(0, 10)
          return(
                  <div key={index}>
                    <h3>{locationName}</h3>
                    <i>dal {startDate} al {endDate}</i>
                    <p>{roleName}</p>
                  </div>
                )
        })
      }
    </div>
);

const print = (props) => {
    //Converti in stringhe i componenti
    const stringSkillSection = renderToString(<SkillSection resumeData={props.resumeData}/>);
    const stringEsperienzeSection = renderToString(<EsperienzeSection resumeData={props.resumeData}/>);
    //Crea gli elementi per il PDF
    const pdf = new jsPDF('p', 'mm', 'a4');
    //Elementi grafici
    //Crea un quadrato di sfondo nella parte destra
    pdf.setFillColor(140, 80, 255);
    pdf.rect(120, 0, 90, 297, 'F');
    //Aggiungi gli elementi al PDF
    //Nome Cognome
    pdf.setFontType("bold");
    pdf.setFontSize(32);
    pdf.text(12, 24, `${props.resumeData.formData.firstName} ${props.resumeData.formData.lastName}`);
    //Descrizione
    pdf.setFontType("italic");
    pdf.setFontSize(14);
    pdf.setTextColor(150);
    pdf.text(12, 32, `${props.resumeData.formData.descrizione}`);
    //Email
    pdf.setFont("courier");
    pdf.setFontType("normal");
    pdf.setFontSize(14);
    pdf.setTextColor(255);
    pdf.text(132, 64, `${props.resumeData.formData.email}`);
    //Telefono
    pdf.text(132, 72, `${props.resumeData.formData.telefono}`);
    //Indirizzo
    pdf.text(132, 84, `${props.resumeData.formData.indirizzo} `);
    pdf.text(132, 90, `${props.resumeData.formData.citta} ${props.resumeData.formData.cap}`)
    //Skill

    pdf.fromHTML(stringSkillSection, 132, 100, {
      color: 'white'
    })

    //Foto
    if (props.resumeData.fotoData !== undefined && props.resumeData.fotoData !== null && props.resumeData.fotoData.length > 0) {
      var img = new Image();
      img.src = props.resumeData.fotoData;
      pdf.addImage(img, 'JPEG', 144, 12);
    }
    let date = new Date("2018-01-01T00:00:00");
    /* Date format you have */
    let dateMDY = `${date.getDate()}${date.getMonth() + 1}${date.getFullYear()}`;
    /* Date converted to MM-DD-YYYY format */
    //Esperienze lavorative e scolastiche
    pdf.fromHTML(stringEsperienzeSection, 12, 54)
    //competenze lavorative
    pdf.save(`${props.resumeData.formData.firstName}_${props.resumeData.formData.lastName}-${dateMDY}`);

    
};

const Resume = (props) => {
    return (
        <div id="resume">
            <h1 className="display-2 fw-bold">Completato!</h1>
            <p className="text-secondary">Clicca il pulsante per scaricare {'\u2728'}</p>
            <Button variant="primary" size="lg" className="rounded-pill mt-3" onClick={(e) => print(props)}>Download</Button>
        </div>
    );
};

export default Resume;