import React, {useState} from "react";
import {Modal, Button, Form, Row, Col} from 'react-bootstrap'
import DatePicker from "react-datepicker";


import "react-datepicker/dist/react-datepicker.css";


export default function FormModal(props) {

  return (
    <Modal
      show={props.show}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      onHide={props.onHide}
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {(props.modalMode === 'factory') ? 'Aggiungi una attività lavorativa' : 'Aggiungi un percorso di studio'}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <p>
          Compila i campi per descrivere al meglio l'esperienza effettuata
        </p>
        {(props.modalMode === 'factory') ? <FormListDateFactory addJob={props.addJob}/> : <FormListDateStudy addStudy={props.addStudy}/>}
      </Modal.Body>
      <Modal.Footer>
        <p className="text-muted">
          Clicca su aggiungi per salvare la nuova attività
        </p>
      </Modal.Footer>
    </Modal>
  );
}

function FormListDateFactory(props) {
  const [locationName, setLocationName] = useState(undefined);
  const [roleName, setRoleName] = useState(undefined);
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());

  return(<div id="form-list-date">
          <Form>
            <Row>
              <Col md="6">
                <Form.Control placeholder="Nome azienda" onChange={e => setLocationName(e.target.value)}/>
              </Col>
              <Col md="6">
                <Form.Control placeholder="Ruolo" onChange={e => setRoleName(e.target.value)}/>
              </Col>
              <hr className="mt-3"/>
              <Col md="6">
                <DatePicker className="form-control" selected={startDate} onChange={(date) => setStartDate(date)} />
              </Col>
              <Col md="6">
                <DatePicker className="form-control" selected={endDate} onChange={(date) => setEndDate(date)} />
              </Col>
            </Row>
            <Col className="d-grid gap-2 mt-2">
              <Button variant="primary" onClick={(e) => props.addJob({locationName, roleName, startDate, endDate})} size="sm">Aggiungi</Button>
            </Col>
          </Form>

        </div>)
}

function FormListDateStudy(props) {
  const [locationName, setLocationName] = useState(undefined);
  const [roleName, setRoleName] = useState(undefined);
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());

  return(<div id="form-list-date">
          <Form>
            <Row>
              <Col md="6">
                <Form.Control placeholder="Nome scuola/ Corso" onChange={e => setLocationName(e.target.value)}/>
              </Col>
              <Col md="6">
                <Form.Control placeholder="Titolo ottenuto" onChange={e => setRoleName(e.target.value)}/>
              </Col>
              <hr className="mt-3"/>
              <Col md="6">
                <DatePicker className="form-control" selected={startDate} onChange={(date) => setStartDate(date)} />
              </Col>
              <Col md="6">
                <DatePicker className="form-control" selected={endDate} onChange={(date) => setEndDate(date)} />
              </Col>
            </Row>
            <Col className="d-grid gap-2 mt-2">
              <Button variant="primary" onClick={(e) => props.addStudy({locationName, roleName, startDate, endDate})} size="sm">Aggiungi</Button>
            </Col>
          </Form>

        </div>)
}