import React, {Component} from "react";
import {Table} from 'react-bootstrap';
import { Icon } from '@iconify/react';

class FormTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dataForTable: this.props.dataForTable,
    }
  }
  
  removeHandler = (n) => {
    this.setState({dataForTable: undefined})
    this.props.removeItem(n)
    this.setState({dataForTable: this.props.dataForTable})

  }


  render() {
    return (<Table striped bordered hover size="sm">
              <thead>
                <tr>
                  <th>{(this.props.mode === 'factory') ? 'Azienda' : 'Scuola'}</th>
                  <th>{(this.props.mode === 'factory') ? 'Ruolo' : 'Titolo'}</th>
                  <th>Inizio</th>
                  <th>Fine</th>
                  <th></th>

                </tr>
              </thead>
              <tbody>
                  {
                    (
                      this.state.dataForTable !== undefined && 
                      this.state.dataForTable !== null && 
                      this.state.dataForTable.length > 0) ? 
                    this.state.dataForTable.map((element,index) => 
                      <tr key={index}>
                        <td>{element.locationName}</td>
                        <td>{element.roleName}</td>
                        <td>{element.startDate.toISOString().slice(0, 10)}</td>
                        <td>{element.endDate.toISOString().slice(0, 10)}</td>
                        <td>
                          <Icon 
                            onClick={e => this.removeHandler(index)} 
                            icon="ri:delete-bin-2-line" />
                        </td>
                      </tr>) 
                    : 
                    <tr><td colSpan={5}>Aggiungi qualcosa...</td></tr>
                  } 

              </tbody>
            </Table>
    );
  }
}

export default FormTable;