import * as React from "react";

import {Container, Row, Col, Button} from 'react-bootstrap'
import { Link } from "react-router-dom";

import LandingImage from '../Image/LandingImage'

export default function Home() {
  return(<Container id="home">
    <Row>
      <Col md={6} className="v-center">
        <h1 className="display-2">Crea il tuo <span className="text-primary">Curriculum Vitae</span></h1>
        <h2>Nel modo più intelligente</h2>
        <Button as={Link} to="create" className="w-25" variant="primary">INIZIA</Button>
      </Col>
      <Col md={6} className="v-center">
        <LandingImage />
      </Col>
    </Row>
  </Container>);
}